.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(0, 212, 255, 0.2);
  transition: all 0.3s ease;
  will-change: transform, backdrop-filter;
}

.header.scrolled {
  background: rgba(0, 0, 0, 0.95);
  box-shadow: 0 2px 20px rgba(0, 212, 255, 0.1);
}

.header-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
}

.logo {
  display: flex;
  align-items: center;
  gap: 1rem;
  text-decoration: none;
  color: #fff;
  font-family: 'Orbitron', monospace;
  font-weight: 700;
  font-size: 1.5rem;
  transition: all 0.3s ease;
}

.logo:hover {
  color: #00d4ff;
  text-shadow: 0 0 10px rgba(0, 212, 255, 0.5);
}

.logo-image {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: linear-gradient(45deg, #00d4ff, #0099cc);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  font-weight: 900;
  color: #000;
  animation: pulse 2s ease-in-out infinite;
  will-change: opacity;
}

.nav {
  display: flex;
  gap: 2rem;
  align-items: center;
}

.nav-links {
  display: flex;
  gap: 2rem;
  list-style: none;
}

.nav-link {
  color: #fff;
  text-decoration: none;
  font-family: 'Rajdhani', sans-serif;
  font-weight: 500;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  position: relative;
  padding: 0.5rem 0;
}

.nav-link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(45deg, #00d4ff, #0099cc);
  transition: width 0.3s ease;
}

.nav-link:hover::after,
.nav-link.active::after {
  width: 100%;
}

.nav-link:hover,
.nav-link.active {
  color: #00d4ff;
  text-shadow: 0 0 5px rgba(0, 212, 255, 0.3);
}

.mobile-menu-toggle {
  display: none;
  flex-direction: column;
  gap: 4px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.5rem;
  z-index: 1001;
}

.menu-line {
  width: 25px;
  height: 3px;
  background: #00d4ff;
  transition: all 0.3s ease;
  border-radius: 2px;
  will-change: transform, opacity;
}

.mobile-menu-toggle.active .menu-line:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}

.mobile-menu-toggle.active .menu-line:nth-child(2) {
  opacity: 0;
}

.mobile-menu-toggle.active .menu-line:nth-child(3) {
  transform: rotate(-45deg) translate(7px, -6px);
}

/* Otimizações para mobile */
@media (max-width: 768px) {
  .header {
    backdrop-filter: blur(5px);
  }

  .header-container {
    padding: 0 1rem;
    height: 70px;
  }

  .logo {
    font-size: 1.3rem;
  }

  .logo-image {
    width: 35px;
    height: 35px;
    font-size: 1rem;
  }

  .nav {
    position: fixed;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.95);
    backdrop-filter: blur(10px);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: right 0.3s ease;
    z-index: 1000;
  }

  .nav.active {
    right: 0;
  }

  .nav-links {
    flex-direction: column;
    gap: 2rem;
    text-align: center;
  }

  .nav-link {
    font-size: 1.5rem;
    padding: 1rem 0;
  }

  .mobile-menu-toggle {
    display: flex;
  }

  /* Otimizar animações em mobile */
  .logo-image {
    animation: pulse 3s ease-in-out infinite;
  }
}

@media (max-width: 480px) {
  .header-container {
    height: 60px;
  }

  .logo {
    font-size: 1.1rem;
  }

  .logo-image {
    width: 30px;
    height: 30px;
    font-size: 0.9rem;
  }

  .nav-link {
    font-size: 1.3rem;
  }

  .menu-line {
    width: 22px;
    height: 2px;
  }

  /* Desabilitar backdrop-filter em dispositivos muito pequenos */
  .header,
  .nav {
    backdrop-filter: none;
    background: rgba(0, 0, 0, 0.98);
  }
}

/* Otimizações de performance */
@media (prefers-reduced-motion: reduce) {
  .header,
  .nav,
  .nav-link,
  .logo,
  .logo-image,
  .menu-line {
    transition: none;
    animation: none;
  }
} 