.footer {
  background: linear-gradient(135deg, #000 0%, #0a0a0a 50%, #000 100%);
  border-top: 1px solid rgba(0, 212, 255, 0.2);
  padding: 3rem 0 1rem;
  position: relative;
  overflow: hidden;
}

.footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 50% 0%, rgba(0, 212, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.footer-content {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  gap: 3rem;
  margin-bottom: 2rem;
}

.footer-section {
  display: flex;
  flex-direction: column;
}

.footer-logo {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

.footer-logo-image {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: linear-gradient(45deg, #00d4ff, #0099cc);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  font-weight: 900;
  color: #000;
}

.footer-logo-text {
  font-family: 'Orbitron', monospace;
  font-weight: 700;
  font-size: 1.5rem;
  color: #fff;
}

.footer-description {
  font-size: 0.9rem;
  color: #ccc;
  line-height: 1.6;
  margin-bottom: 1.5rem;
  max-width: 400px;
}

.footer-title {
  font-family: 'Orbitron', monospace;
  font-size: 1.3rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  color: #00d4ff;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.footer-social {
  display: flex;
  gap: 1rem;
}

.footer-social-link {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(0, 212, 255, 0.1);
  border: 1px solid rgba(0, 212, 255, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #00d4ff;
  font-size: 1.2rem;
  text-decoration: none;
  transition: all 0.3s ease;
  will-change: transform, box-shadow;
}

.footer-social-link:hover {
  background: rgba(0, 212, 255, 0.2);
  border-color: #00d4ff;
  box-shadow: 0 0 15px rgba(0, 212, 255, 0.3);
  transform: translateY(-2px);
}

.footer-section-title {
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
  font-size: 1.2rem;
  color: #fff;
  margin-bottom: 1rem;
}

.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-link {
  margin-bottom: 0.8rem;
}

.footer-link a {
  color: #ccc;
  text-decoration: none;
  font-size: 0.9rem;
  transition: all 0.3s ease;
  will-change: color;
}

.footer-link a:hover {
  color: #00d4ff;
  text-shadow: 0 0 5px rgba(0, 212, 255, 0.3);
}

.footer-contact {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.footer-contact-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #ccc;
  font-size: 0.9rem;
}

.footer-contact-icon {
  color: #00d4ff;
  font-size: 1rem;
}

.footer-bottom {
  border-top: 1px solid rgba(0, 212, 255, 0.1);
  padding-top: 1.5rem;
  text-align: center;
}

.footer-copyright {
  color: #aaa;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}

.footer-credits {
  color: #888;
  font-size: 0.7rem;
}

.footer-credits a {
  color: #00d4ff;
  text-decoration: none;
  transition: all 0.3s ease;
  will-change: color;
}

.footer-credits a:hover {
  color: #fff;
  text-shadow: 0 0 5px rgba(0, 212, 255, 0.3);
}

/* Otimizações para mobile */
@media (max-width: 768px) {
  .footer {
    padding: 2rem 0 1rem;
  }

  .footer-container {
    padding: 0 1rem;
  }

  .footer-content {
    grid-template-columns: 1fr;
    gap: 2rem;
    text-align: center;
  }

  .footer-logo {
    justify-content: center;
  }

  .footer-description {
    margin: 0 auto 1.5rem;
  }

  .footer-social {
    justify-content: center;
  }

  .footer-social-link {
    width: 35px;
    height: 35px;
    font-size: 1rem;
  }

  .footer-section-title {
    font-size: 1.1rem;
  }

  .footer-link a {
    font-size: 0.85rem;
  }

  .footer-contact-item {
    font-size: 0.85rem;
    justify-content: center;
  }

  .footer-contact-icon {
    font-size: 0.9rem;
  }

  .footer-copyright {
    font-size: 0.75rem;
  }

  .footer-credits {
    font-size: 0.65rem;
  }

  /* Otimizar animações em mobile */
  .footer-social-link:hover {
    transform: translateY(-1px);
  }
}

@media (max-width: 480px) {
  .footer {
    padding: 1.5rem 0 1rem;
  }

  .footer-content {
    gap: 1.5rem;
  }

  .footer-logo-text {
    font-size: 1.3rem;
  }

  .footer-logo-image {
    width: 35px;
    height: 35px;
    font-size: 1rem;
  }

  .footer-description {
    font-size: 0.8rem;
  }

  .footer-social-link {
    width: 30px;
    height: 30px;
    font-size: 0.9rem;
  }

  .footer-section-title {
    font-size: 1rem;
  }

  .footer-link a {
    font-size: 0.8rem;
  }

  .footer-contact-item {
    font-size: 0.8rem;
  }

  .footer-contact-icon {
    font-size: 0.8rem;
  }

  .footer-copyright {
    font-size: 0.7rem;
  }

  .footer-credits {
    font-size: 0.6rem;
  }

  /* Desabilitar animações complexas em dispositivos muito pequenos */
  .footer-social-link:hover {
    transform: none;
  }
}

/* Otimizações de performance */
@media (prefers-reduced-motion: reduce) {
  .footer-social-link,
  .footer-link a,
  .footer-credits a {
    transition: none;
  }
} 