@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700;900&family=Rajdhani:wght@300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Rajdhani', sans-serif;
  background: #000;
  color: #fff;
  overflow-x: hidden;
  /* Otimizações para mobile */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

.App {
  background: linear-gradient(135deg, #000 0%, #0a0a0a 50%, #000 100%);
  min-height: 100vh;
  /* Otimizações para mobile */
  will-change: auto;
  transform: translateZ(0);
}

.app-container {
  position: relative;
}

/* Scrollbar personalizada - otimizada para mobile */
::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  background: #000;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(45deg, #00d4ff, #0099cc);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(45deg, #0099cc, #00d4ff);
}

/* Animações globais - otimizadas para performance */
@keyframes glow {
  0%, 100% { 
    box-shadow: 0 0 5px #00d4ff, 0 0 10px #00d4ff, 0 0 15px #00d4ff; 
  }
  50% { 
    box-shadow: 0 0 10px #00d4ff, 0 0 20px #00d4ff, 0 0 30px #00d4ff; 
  }
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-8px); }
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.8; }
}

@keyframes slideInFromLeft {
  0% {
    transform: translateX(-50px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideInFromRight {
  0% {
    transform: translateX(50px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideInFromBottom {
  0% {
    transform: translateY(30px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Classes utilitárias - otimizadas */
.glow-effect {
  animation: glow 3s ease-in-out infinite alternate;
  will-change: box-shadow;
}

.float-animation {
  animation: float 4s ease-in-out infinite;
  will-change: transform;
}

.pulse-animation {
  animation: pulse 2s ease-in-out infinite;
  will-change: opacity;
}

.slide-in-left {
  animation: slideInFromLeft 0.8s ease-out;
}

.slide-in-right {
  animation: slideInFromRight 0.8s ease-out;
}

.slide-in-bottom {
  animation: slideInFromBottom 0.8s ease-out;
}

/* Gradientes e efeitos - otimizados */
.cyber-gradient {
  background: linear-gradient(45deg, #00d4ff, #0099cc, #0066ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.neon-border {
  border: 2px solid transparent;
  background: linear-gradient(45deg, #00d4ff, #0099cc) border-box;
  -webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
}

.glass-effect {
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(8px);
  border: 1px solid rgba(0, 212, 255, 0.2);
}

/* Otimizações para mobile */
@media (max-width: 768px) {
  body {
    font-size: 14px;
  }
  
  /* Reduzir complexidade das animações em mobile */
  .glow-effect {
    animation: glow 4s ease-in-out infinite alternate;
  }
  
  .float-animation {
    animation: float 5s ease-in-out infinite;
  }
  
  /* Otimizar backdrop-filter para mobile */
  .glass-effect {
    backdrop-filter: blur(4px);
  }
}

@media (max-width: 480px) {
  body {
    font-size: 12px;
  }
  
  /* Desabilitar algumas animações em dispositivos muito pequenos */
  .glow-effect {
    animation: none;
    box-shadow: 0 0 5px #00d4ff;
  }
  
  .glass-effect {
    backdrop-filter: none;
    background: rgba(0, 0, 0, 0.3);
  }
}

/* Otimizações de performance */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}
